

body {
  background:#89609E;   
  background-size: cover;  
  text-align: center;
  margin: 100px auto;
  width: 80%;
  padding: 0 40px;
  font: 14px normal 'Varela Round', Helvetica, serif;
}

.logo {
  display: inline-block;
  background: url(./images/logo.png) 0 0 no-repeat;  
  background-size: contain;
  height: 62px;
  width: 198px;
  vertical-align: middle;
}

small {
   font-size: 80%; 
   line-height: 40px;
   color: rgba(255, 255, 255, 0.5);
}

h3 {
  color: #fff;
  font-weight: bold;
  font-size: 1.17em;
  margin-bottom: 0;
  text-align: left;
  padding: 2%;
  
}
.list {
  background: rgba(222, 225, 227, 0.9);
  border-radius: 3px;
  padding: 0.75em;
  cursor: -webkit-grab;
  cursor: grab;
}
.input   {
  width: 100%;
  border: 0px none;
  padding: 1.8em;
  font-size: 1.1em;
  font-weight: bold;
  margin-bottom: 2%;
}

.button {
    width: 100%;
    background: transparent;
    color: #fff;
    border: 1px solid #fff;
    padding: 1em;
    font-size: 1.2em;
    line-height: 1.2em;
    -webkit-box-sizing: inherit;
            box-sizing: inherit;
    margin-bottom: 2.3em;

}

.button:hover {
    color: #89609E;
    background: #fff;

}
.footer {
   width: 69%;
   position: absolute;
   bottom: 0px;

}
.board {
  margin-left: 0px;
}
.boards {
  background: rgba(222, 225, 227, 0.9);
  border-radius: 3px;
  margin: 0 0.5em 1em 0.5em;
  width: 100%;
  line-height: 1.75em;
  overflow: hidden;
  padding: 0.75em;
  min-height: 5em;
  cursor: pointer;
}
.card {
  background: #fff;
  padding: 0.5em;
  border-radius: 3px;
  border-bottom: 1px solid #bdc3c7;
}


